#courseBulletinPage{
    margin: 3em 8em;
    > p{
        font-size:larger;
        font-weight: 600;
    }
    .courseBulletin{
        .courseBulletinContent{
            background-color: #f0f0f0;
            display: flex;
            flex-direction: row;
            padding: 1em 1em;
            margin: 2em 0em;
            .cbPin{
                margin-right: 1rem;
                font-size: large;
            }
            .cb{
                flex-direction: column;
                .cbTitle{
                    font-size: large;
                    margin-top: 0;
                    font-weight: 600;
                }
                .cbContent{
                    display: flex;
                    flex-direction: row;
                    img{
                        width: 3em;
                        height: 3em;
                        margin-right: 1em;
                    }
                    div {
                        .cbAuther{
                            font-size: large;
                            font-weight: 600;
                            margin: 0em 0em ;
                        }
                        .cbTime {
                            font-size: 15px;
                            margin: 0em 0em ;
                            font-weight: 200;
                        }   
                        .cbInfor{
                            font-size: medium;
                            font-weight: 400;
                        }
                        
                    }
                }
        
            }
            
        }

    }
}